import React from 'react'

const Front = (props) => (

    <div className="front__card">
        <img src={`${props.info.thumbNail}`} alt=""/>
        <p class="text__sm text__light">{props.info.date}</p>
        <h3>{props.info.title}</h3>
    
        {props.info.tag.map((item, index) => (
                <p>{item}</p>
        ))}

    </div>

)

export default Front;